<template>
  <div class="maikeBox" ref="ifrem">

  </div>
</template>

<script>
import maikeCode from './maikeCode.js';

export default {
  props: {
    formId: {
       type: String,
       default: ''
    }
  },
  created () {
      maikeCode(this.formId);
  },
  mounted () {
    let maike = document.querySelector(`#mkin${this.formId}`);
    this.$refs.ifrem.append(maike);
  },
  components: {
  }
}
</script>

<style lang="less" scoped>
div {
  height: auto;
}

</style>



